import {Component, OnInit} from '@angular/core';
import {Command} from '../../../models/command.model';
import {CommandAutoService} from '../../../services/commandAuto.service';
import {Router} from '@angular/router';
import {CommandsToPrepareService} from '../../../services/commandsToPrepare.service';
import {CanalService} from '../../../services/canal.service';

@Component({
  selector: 'app-history',
  templateUrl: './history.component.html',
  styleUrls: ['./history.component.scss'],
})
export class HistoryComponent implements OnInit {
  getAllCommandesReceived: Command[] | null = null;
  constructor(
    private autoService: CommandAutoService,
    private router: Router,
    private commandsToPrepareService: CommandsToPrepareService,
    private canalService: CanalService,
  ) {
  }

  ngOnInit() {
    this.getAllCommands(this.canalService.getUserIdFoCommand());
  }

  getAllCommands(userId: number) {
    this.commandsToPrepareService.allCommandsInversedByUserId(userId).subscribe(
      (data) => {
        this.getAllCommandesReceived = data; // Mettez Ã  jour les donnÃ©es dÃ¨s qu'elles sont disponibles ou mises Ã  jour
      },
      (error) => {
        console.error('Erreur lors de la rÃ©cupÃ©ration des donnÃ©es:', error);
      }
    );
  }

  goToDetailsCommand(id: number) {
    this.router.navigate(['command/history/details/', id]);
  }
}
