import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {interval, Observable, Subject} from 'rxjs';
import {tap, switchMap, startWith, scan} from 'rxjs/operators';


@Injectable({
  providedIn: 'root',
})
export class CommandAutoService {
  private apiUrl = 'https://oafish-orange-production.up.railway.app/api/command/commands/';
  //private apiUrl = 'http://192.168.0.246:8080/api/command/commands/';
  private refreshInterval = 5000; // Temps d'actualisation en millisecondes (ici, toutes les 5 secondes)
  // eslint-disable-next-line @typescript-eslint/member-ordering
  public notificationSound = new Audio('assets/audio/songOrder.mp3');
  private currentNumberOfCommandes = 0;
  private previousCommandes: any[] = [];

  private allCommandReceivedSubject = new Subject<any[]>();
  private allCommandReceived$: Observable<any[]>;

  private allCommandReceivedInOrderSubject = new Subject<any[]>();
  private allCommandReceivedInOrder$: Observable<any[]>;

  private commandReceivedTodaySubject = new Subject<any[]>();
  private commandReceivedToday$: Observable<any[]>;

  private commandReceivedYesterdaySubject = new Subject<any[]>();
  private commandReceivedYesterday$: Observable<any[]>;

  private commandReceivedThisWeekSubject = new Subject<any[]>();
  private commandReceivedThisWeek$: Observable<any[]>;

  private commandReceivedThisMonthSubject = new Subject<any[]>();
  private commandReceivedThisMonth$: Observable<any[]>;
  /*-----------------------*/
  private allSumPricecommandSubject = new Subject<number>();
  private allSumPricecommand$: Observable<number>;

  private sumPricecommandTodaySubject = new Subject<number>();
  private sumPricecommandToday$: Observable<number>;

  private sumPricecommandYesterdaySubject = new Subject<number>();
  private sumPricecommandYesterday$: Observable<number>;

  private sumPricecommandThisWeekSubject = new Subject<number>();
  private sumPricecommandThisWeek$: Observable<number>;

  private sumPricecommandThisMonthSubject = new Subject<number>();
  private sumPricecommandThisMonth$: Observable<number>;

  private deleteCommandSubject = new Subject<number>();
  private deleteCommand$: Observable<number>;

  constructor(private http: HttpClient) {
    this.allCommandReceived$ = this.allCommandReceivedSubject.asObservable();
    this.allCommandReceivedInOrder$ = this.allCommandReceivedInOrderSubject.asObservable();
    this.commandReceivedToday$ = this.commandReceivedTodaySubject.asObservable();
    this.commandReceivedYesterday$ = this.commandReceivedYesterdaySubject.asObservable();
    this.commandReceivedThisWeek$ = this.commandReceivedThisWeekSubject.asObservable();
    this.commandReceivedThisMonth$ = this.commandReceivedThisMonthSubject.asObservable();
    /*-----------------------*/
    this.allSumPricecommand$ = this.allSumPricecommandSubject.asObservable();
    this.sumPricecommandToday$ = this.sumPricecommandTodaySubject.asObservable();
    this.sumPricecommandYesterday$ = this.sumPricecommandYesterdaySubject.asObservable();
    this.sumPricecommandThisWeek$ = this.sumPricecommandThisWeekSubject.asObservable();
    this.sumPricecommandThisMonth$ = this.sumPricecommandThisMonthSubject.asObservable();
    this.deleteCommand$ = this.deleteCommandSubject.asObservable();
    this.startPolling();
  }

  private startPolling() {
    this.allCommandReceived$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'ordre-inverse')),
      tap((data) => this.allCommandReceivedSubject.next(data))
    );

    this.allCommandReceivedInOrder$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'getAll')),
      //scan((acc, val) => val), // Utilisez scan pour accumuler les nouvelles donnÃ©es
      tap((data) => {

      }),

    );
    /*this.commandReceivedToday$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'added-today')),
      tap((data) => this.commandReceivedTodaySubject.next(data))
    );*/

  this.commandReceivedToday$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'added-today')),
      tap((data) => {

        this.commandReceivedTodaySubject.next(data); // Ã‰mettez la nouvelle commande
        if (data.length > 0) {
          this.notificationSound.play(); // Envoyez une notification
        }
      })
    );

    this.commandReceivedYesterday$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'added-yesterday')),
      tap((data) => this.commandReceivedYesterdaySubject.next(data))
    );

    this.commandReceivedThisWeek$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'added-this-week')),
      tap((data) => this.commandReceivedThisWeekSubject.next(data))
    );

    this.commandReceivedThisMonth$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(this.apiUrl + 'added-this-mounth')),
      tap((data) => this.commandReceivedThisMonthSubject.next(data))
    );
    /*------------------------*/
    this.allSumPricecommand$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<number>(this.apiUrl + 'somme-prix-total')),
      tap((data) => this.allSumPricecommandSubject.next(data))
    );

    this.sumPricecommandToday$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<number>(this.apiUrl + 'sum-price-today')),
      tap((data) => this.sumPricecommandTodaySubject.next(data))
    );

    this.sumPricecommandYesterday$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<number>(this.apiUrl + 'sum-price-yesterday')),
      tap((data) => this.sumPricecommandYesterdaySubject.next(data))
    );

    // @ts-ignore
    this.sumPricecommandThisWeek$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<number>(this.apiUrl + 'sum-price-thisweek')),
      tap((data) => this.sumPricecommandThisWeekSubject.next(data))
    );

    this.sumPricecommandThisMonth$ = interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<number>(this.apiUrl + 'sum-price-thismounth')),
      tap((data) => this.sumPricecommandThisMonthSubject.next(data))
    );
  }


  // Nouvelle mÃ©thode pour effectuer une requÃªte HTTP DELETE
  // eslint-disable-next-line @typescript-eslint/member-ordering
  deleteCommande(commandeId: number): Observable<any> {
    const deleteUrl = `${this.apiUrl}/${commandeId}`;
    return this.http.delete<any>(deleteUrl);
  }
  // eslint-disable-next-line @typescript-eslint/member-ordering
  getAllCommandesReceived(): Observable<any[]> {
    return this.allCommandReceived$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  getAllCommandesReceivedInOrder(): Observable<any[]> {
    return this.allCommandReceivedInOrder$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  commandReceivedToday(): Observable<any[]> {
    return this.commandReceivedToday$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  commandReceivedYesterday(): Observable<any[]> {
    return this.commandReceivedYesterday$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  commandReceivedThisWeek(): Observable<any[]> {
    return this.commandReceivedThisWeek$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  commandReceivedThisMonth(): Observable<any[]> {
    return this.commandReceivedThisMonth$;
  }

  /*---------------------------*/

  // eslint-disable-next-line @typescript-eslint/member-ordering
  allSumPricecommand(): Observable<number> {
    return this.allSumPricecommand$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  sumPricecommandToday(): Observable<number> {
    return this.sumPricecommandToday$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  sumPricecommandYesterday(): Observable<number> {
    return this.sumPricecommandYesterday$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  sumPricecommandThisWeek(): Observable<number> {
    return this.sumPricecommandThisWeek$;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  sumPricecommandThisMonth(): Observable<number> {
    return this.sumPricecommandThisMonth$;
  }
}
